* {
    margin: 0;
    padding: 0;
}

/* Home Page */

body {
    font-family: 'Roboto', sans-serif;
    color: rgba(255, 255, 255, 0.92);
    background-color: #000000;
}

.web {
    width: 100%;
    height: 45rem;
}


/* --------------- */

@media (min-width: 500px) {
    .Introduction {
        height: 100%;
        display: flex;
        flex-flow: row wrap;
        justify-content: center;
        align-items: center;
        align-content: flex-start;
        /*border: 2px solid red;*/
    }
    div h1 {
        display: block;
        width: 95%;
        font-size: 1.55rem;
        font-weight: bold;
        line-height: 1.2;
        margin-top: 1rem;
        padding: 0.5rem 0.5rem;
        /*border: 2px solid white;*/
    }
    nav {
        width: 95%;
        margin: 0.5rem 0rem;
        padding: 0 0.5rem;
        /*border: 2px solid blue;*/
    }
    .p2 {
        display: block;
        width: 95%;
        font-size: 1.6rem;
        font-weight: 500;
        line-height: 1.2;
        margin: 2rem 0;
        padding: 0.5rem 0.5rem;
        /*border: 2px solid red;*/
    }
}


/* --------------- */


@media (min-width: 615px) {
    div h1, nav {
        min-width: 600px;
    }
    .p2 {
        min-width: 605px;
    }
    .Introduction {
        align-content: center;
    }
    div h1 {
        width: 63%;
        /*font-size: 1.95rem;*/
        font-size: 2.21rem;
        margin-top: -1rem;
    }
    nav {
        width: 63%;
    }
    .p2 {
        width: 63%;
    }
}




/* --------------- */

@media (min-width: 615px) {
    div h1, nav {
        min-width: 600px;
    }
    .p2 {
