}

/* Desktop @media */

@media (min-width: 945px) {
    div h1, nav {
        min-width: 900px;
    }
    .p2 {
        min-width: 905px;
    }
    div h1 {
        font-size: 2.6rem;
    }
}

nav ul {
    display: inline;
    /*border: 2px solid red;*/
}

nav li {
    display: inline-block;
    border-radius: 25px;
    margin: 0 1.5rem 0 0rem;   
    padding: 0.4rem 0.7rem 0.4rem 0.7rem;
    background-color: rgba(255, 255, 255,0.12);
    /*border: 2px solid pink;*/
}

nav a {
    text-decoration: none;
    color: rgba(255, 255, 255, 0.92);
    padding: 0.2rem 0.2rem;
    font-size: 1rem;
    font-weight: bold;
}

nav li:hover {
    background-color:rgba(255, 255, 255, 0.171);
}



























/* CV Page */

.background {
    background-color: #222;
    width: 100%;
    height: 100%;
}

.cvWidth {
    display: flex;
    flex-flow: row wrap;
    justify-content: center; /* Horizontal */
    /*border: 2px solid blue;*/
}

.CV {
    display: block;
    width: 32%;
    padding: 0px 0px 0px 10px;
    min-width: 500px;
    margin-top: 4rem;
    font-size: 14px;
    line-height: 1.5;
    /* border: 2px solid red; */
}


div {
    padding: 0 0 0 0;
    margin: 0 0 3rem 0;
}

.profilePic {
    vertical-align: middle;
    width: 95px;
    height: 95px;
    border-radius: 50%;
    float: left;
    margin-right: 15px;
    /*border: 2px solid pink;*/
}

.floatRight {
    float: right;
}

figure {
    padding-top: 7px;
    /*border: 2px solid blue;*/
}

h2 {
    font-size: 20px;
    font-weight: 400;
    color: #eee;
    margin: 0;
}

.About {
    font-size: 15px;
    color: #eee;
    margin: 0;
    margin-bottom: 0.5rem;
}

.White, aside a {
    font-size: 15px;
    color: #eee;
    margin: 0;
    margin-bottom: 1rem;
}

.LightGrey {
    color: #aaa;
    margin: 0;
}

.Grey {
    color: #777;
    margin: 0;
}

li {
    margin-bottom: 4rem;
}


div ul {
    list-style: none;
    width: 23%;
    max-height: 4.1rem;
    padding-left: 0;

}

aside {
    width: 76%;
    float: right;
}

aside p {
    margin: 0;
}




